            this.treeDataOptions[3] =this.buildForm.bi_station_id;
          }else{
            this.treeDataOptions[2] =this.buildForm.bi_station_id;
          }
        }
        this.buildForm.bi_build_number = row.bi_build_number;
        this.buildForm.bi_longitude = row.bi_longitude;
        this.buildForm.bi_latitude = row.bi_latitude;
        this.buildForm.bi_build_year = row.bi_build_year;
        this.buildForm.bi_build_type = row.bi_build_type;
        this.buildForm.bi_unit_count = row.bi_unit_count;
        this.buildForm.bi_memo = row.bi_memo;
        this.buildForm.bi_uuid = row.bi_uuid;
        this.buildForm.flag = '2';
        this.buildDialogFormVisible = true;

      },
      handleDeleteRow(index, row) {
        console.log("行号："+index+",行内容：" + JSON.stringify(row) );
        this.buildForm.tpui_count = row.tpui_count;
        if(row.tpui_count !== 0 ){
          this.$alert('当前要删除楼栋[<span style="color:red;font-size: 20px;">' + row.bi_build_number + '</span>]的用户数量不为零，不可以删除', '删除提示', {
            dangerouslyUseHTMLString: true
          });
        }else {
          this.$confirm('请再次确认是否要删除[<span style="color:red;">' + row.bi_build_number + '</span>]该行记录?', '删除提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning',
            dangerouslyUseHTMLString: true,
            closeOnClickModal: false,
            closeOnPressEscape: false,

          }).then(() => {
            let _jsondata_ =   {
              bi_uuid:row.bi_uuid,
            } ;
            var p1 = this.customGlobal.postHttpClient(
              this.customGlobal.serverUrl.residentialBuildings_delete,
              //this.customGlobal.serverUrl.baseURI,
              this.customGlobal.serverUrl.orgBaseURI,
              {"Content-Type": "application/x-www-form-urlencoded"},
              this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
            );
            p1.then((res) => {
              console.log("异步调用成功返回:"+JSON.stringify(res));
              if(res.data.status=='success'){
                this.$message({
                  type: 'success',
                  showClose: true,
                  duration:3000,
                  dangerouslyUseHTMLString: true,
                  message: '<span style="color:black;">基础信息 ['+row.bi_build_number+'] 删除成功 </span>'
                });
                this.reQueryValveCurrentData();
              }else{
                console.info("删除失败");
                this.$alert(res.data.msg, '删除失败', {
                  confirmButtonText: '确定',
                  callback: action => {
                    console.info('data:'+JSON.stringify(res.data));
                    if(res.data.msg==='请登录认证'){
                      this.$router.push({path:'/login'});
                    }
                  }
                });
              }
            },error=>{
              console.log("异步调用出错:"+JSON.stringify(error));
              this.$alert(JSON.stringify(error.message), '删除出错!', {
                confirmButtonText: '确定',
                callback: action => {
                }
              });
            }).catch((res) => {
              console.log("异步调用失败返回:"+JSON.stringify(res));
              this.$alert(JSON.stringify(res.message), '删除出错', {
                confirmButtonText: '确定',
                callback: action => {
                }
              });
            });
          }).catch((res) => {
            console.log("取消删除:"+JSON.stringify(res));
            this.$message({
              type: 'info',
              showClose: true,
              duration:3000,
              dangerouslyUseHTMLString: true,
              message: '已取消删除'
            });
          });
        }
      },
      getQueryQuarters(bi_station_id){
        let _jsondata_ =   {
          ci_station_id:bi_station_id
        } ;

        var p1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.residentialQuarters_query_data,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
        );
        p1.then((res) => {
          console.log("异步调用成功返回:"+JSON.stringify(res));
          if(res.data.status=='success'){
            this.queryQuarters = res.data.rows;
          }else{
            console.info("查询小区列表失败");
            this.$alert(res.data.msg, '查询小区列表失败', {
              confirmButtonText: '确定',
              callback: action => {
                console.info('data:'+JSON.stringify(res.data));
                if(res.data.msg==='请登录认证'){
                  this.$router.push({path:'/login'});
                }
              }
            });
          }
        },error=>{
          console.log("异步调用出错:"+JSON.stringify(error));
          this.$alert(JSON.stringify(error.message), '查询小区列表出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:"+JSON.stringify(res));
          this.$alert(JSON.stringify(res.message), '查询小区列表出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let that = this;

            let _jsondata_ =   {
              bi_uuid:that.buildForm.bi_uuid,
              bi_company_id :that.buildForm.bi_company_id ,   //所属热力公司id    this.treeNodeInfo.treeNodeCaptions
              bi_child_company_id :that.buildForm.bi_child_company_id ,     // 所属热力分公司id    05cbed1accee4fdab9bedd2b8c61ebb3' ;
              bi_institute_id :that.buildForm.bi_institute_id ,     // 所属热力分公司id    05cbed1accee4fdab9bedd2b8c61ebb3' ;
              bi_station_id :that.buildForm.bi_station_id ,     // 所属热力分公司id    05cbed1accee4fdab9bedd2b8c61ebb3' ;
              bi_community_id : that.buildForm.bi_community_id,
              bi_build_name:that.buildForm.bi_build_name,
              bi_build_number:that.buildForm.bi_build_number,
              bi_build_year:that.buildForm.bi_build_year,
              bi_build_type:that.buildForm.bi_build_type,
              bi_longitude:that.buildForm.bi_longitude,
              bi_latitude:that.buildForm.bi_latitude,
              bi_unit_count:that.buildForm.bi_unit_count,
              bi_memo: that.buildForm.bi_memo,
              flag : that.buildForm.flag
            } ;
            const loading = this.$loading({
              lock: true,
              text: '提交中...',
              target: '.main',
              spinner: 'el-icon-loading',

              background: 'rgba(0, 0, 0, 0)'
            });

            if(that.buildForm.flag === '1'){
              var p1 = this.customGlobal.postHttpClient(
                this.customGlobal.serverUrl.residentialBuildings_add,
                //this.customGlobal.serverUrl.baseURI,
                this.customGlobal.serverUrl.orgBaseURI,
                {"Content-Type": "application/x-www-form-urlencoded"},
                this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
              );
              p1.then((res) => {
                console.log("异步调用成功返回:"+JSON.stringify(res));
                if(res.data.status=='success'){
                  this.buildDialogFormVisible =false;
                  loading.close();
                  this.$message({
                    type: 'success',
                    showClose: true,
                    duration:3000,
                    dangerouslyUseHTMLString: true,
                    message: '<span style="color:black;">基础信息 ['+that.buildForm.bi_build_number+'] 新增成功 </span>'
                  });
                  this.queryValveCurrentData();
                }else{
                  console.info("新增失败");
                  loading.close();
                  this.$alert(res.data.msg, '新增失败', {
                    confirmButtonText: '确定',
                    callback: action => {
                      console.info('data:'+JSON.stringify(res.data));
                      if(res.data.msg==='请登录认证'){
                        this.$router.push({path:'/login'});
                      }
                    }
                  });
                }

              },error=>{
                console.log("异步调用出错:"+JSON.stringify(error));
                loading.close();
                this.$alert(JSON.stringify(error.message), '新增出错!', {
                  confirmButtonText: '确定',
                  callback: action => {

                  }
                });
              }).catch((res) => {
                console.log("异步调用失败返回:"+JSON.stringify(res));
                loading.close();
                this.$alert(JSON.stringify(res.message), '新增出错', {
                  confirmButtonText: '确定',
                  callback: action => {

                  }
                });
              });
              this.queryValveCurrentData();
            }else{
              var p1 = this.customGlobal.postHttpClient(
                this.customGlobal.serverUrl.residentialBuildings_update,
                //this.customGlobal.serverUrl.baseURI,
                this.customGlobal.serverUrl.orgBaseURI,
                {"Content-Type": "application/x-www-form-urlencoded"},
                this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
              );
              p1.then((res) => {
                console.log("异步调用成功返回:"+JSON.stringify(res));
                if(res.data.status=='success'){
                  this.buildDialogFormVisible =false;
                  loading.close();
                  this.$message({
                    type: 'success',
                    showClose: true,
                    duration:3000,
                    dangerouslyUseHTMLString: true,
                    message: '<span style="color:black;">基础信息 ['+that.buildForm.bi_build_number+'] 修改成功 </span>'
                  });
                  this.queryValveCurrentData();
                }else{
                  console.info("修改失败");
                  loading.close();
                  this.$alert(res.data.msg, '修改失败', {
                    confirmButtonText: '确定',
                    callback: action => {
                      console.info('data:'+JSON.stringify(res.data));
                      if(res.data.msg==='请登录认证'){
                        this.$router.push({path:'/login'});
                      }
                    }
                  });
                }

              },error=>{
                console.log("异步调用出错:"+JSON.stringify(error));
                loading.close();
                this.$alert(JSON.stringify(error.message), '修改出错!', {
                  confirmButtonText: '确定',
                  callback: action => {

                  }
                });
              }).catch((res) => {
                console.log("异步调用失败返回:"+JSON.stringify(res));
                loading.close();
                this.$alert(JSON.stringify(res.message), '修改出错', {
                  confirmButtonText: '确定',
                  callback: action => {
                  }
                });
              });
              this.queryValveCurrentData();
            }
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      removeTreeNode (node, data) {
        const parent = node.parent
        const children = parent.data.detail || parent.data
        const index = children.findIndex(d => d.id === data.id)
        children.splice(index, 1)
      },
      initTreeDataStatus(h, { node, data, store }) {

        if(JSON.stringify(this.treeDataStatus)==="{}"){
          return(
            <span style="font-size:14px;">
            <span>{data.caption }</span>
            </span>
        )
        }else{
          let find = false;
          for (let k of Object.keys(this.treeDataStatus)) {
            if(data.id===k){
              //console.info(k+",显示"+data.caption );
              return(
                <span >
                <span>{data.caption }</span>
                </span>
            )
              find = true;
              break;
            }else{
            }
          }
          if(find === false){
            this.removeTreeNode( node,data);
          }
        }
      },
      initOrgTemplateInfo(){
        const loading = this.$loading({
          lock: true,
          text: '查询中...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        let p1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.postQueryOrganTemplate,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata:JSON.stringify({config:'all',username:this.customGlobal.userInfoExt.cui_login_id})})
        );

        p1.then((res) => {
          console.log("异步调用成功返回:"+JSON.stringify(res));
          if(res.data.status=='success'){
            if( this.customGlobal.userInfoExt.cui_type !== '0' ){
              this.treeData = JSON.parse(res.data.organ).detail;
            }else{
              this.treeData =  [JSON.parse(res.data.organ)];
            }
            this.treeDataStatus = JSON.parse(res.data.orgkey);
            loading.close();

          }else{
            console.info("查询失败");
            this.treeData = [ JSON.parse(res.data.organ) ];
            this.treeDataStatus = JSON.parse(res.data.orgkey);
            loading.close();
          }
        },error=>{
          console.log("异步调用出错:"+JSON.stringify(error));
          loading.close();
          this.$alert(JSON.stringify(error.message), '查询出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:"+JSON.stringify(res));
          loading.close();

          this.$alert(JSON.stringify(res.message), '查询出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      }
    }
  }
</script>

